@use 'additional/mixins' as *;

.audio {
  &__inner {
    max-width: 500px;
  }
  &__title {
    text-align: center;
    margin-bottom: 40px;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 30px;
  }
  &__item {
  }
  &__link {
    border-radius: 10px;
    overflow: hidden;
  }
}
@include breakpoint-down(medium) {
  .audio {
    &__title {
      margin-bottom: 25px;
    }

    &__inner {
      margin-inline: auto;
    }
  }
}
