@use 'additional/mixins' as *;

html {
  font-size: 1rem;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.2em;
  color: var(--clr-white);
  background-color: var(--clr-mainColor);
  min-height: 100%;
  letter-spacing: 0.04em;
  display: grid;
  grid-template-rows: 1fr min-content;
  align-content: start;
  overflow-x: hidden;
}

.container {
  max-width: var(--container);
  width: min(calc(100% - 30px), 1155px);
  margin-inline: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2em;
  font-weight: 400;
}

h1 {
  font-size: clamp(1.9rem, 5vw + 0.1rem, 3.125rem);
  font-weight: 400;
}

h2 {
  font-size: clamp(1.7rem, 5vw + 0.1rem, 2.5rem);
  font-weight: 500;
  letter-spacing: 0.04em;
  text-transform: uppercase;

}

h3 {
}

p {
  line-height: 1.2em;
}

section {
  // --section-pd: 100px;
  padding: var(--section-pd) 0;
}

a {
  display: block;
}

@for $i from 1 through 5 {
  section:nth-child(#{$i}) {
    @include adaptivValue('--section-pd', 50, 15, 1155, 375);
  }
}
