@use 'additional/mixins' as *;

.projects {
  &__inner {
  }
  &__title {
    text-align: center;
    margin-bottom: 40px;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(min(100%, 375px), 1fr));
    gap: 35px;
    // height: 100%;
  }
  &__item {
    border-radius: 10px;
    overflow: hidden;
    max-width: 560px;
    justify-self: center;

    height: 315px;
    width: 100%;
    iframe {
      width: 100%;
      height: 100%;
    }
  }
  &__link {
  }
}


