@use 'additional/mixins' as *;

.about {
  &__inner {
  }
  &__title {
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 50px;
  }
  &__text {
    line-height: 1.3em;
    font-size: 1.375rem;
    color: rgba(255,255, 255, .5);

    a {
      color: #eac381;
      display: inline;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

@include breakpoint-down(medium) {
  .about {
    &__title {
      margin-bottom: 25px;
    }
  }
}
