@use 'sass:color';
@use 'mixins' as *;
@use 'colors' as *;
@use 'font-styles' as *;
@use 'z-index' as *;

:root {
  --ff-1: 'Roboto', sans-serif;
  --container: 1155px;

  @each $name, $value in $colors {
    --clr-#{$name}: #{$value};
  }

  @each $screen-size in $breakpoints-down {
    @if $screen-size == large {
    } @else if $screen-size == extra-big {
      @include breakpoint-down(extra-big) {
        --container: 1199.98px;
      }
    } @else if $screen-size == big {
      @include breakpoint-down(big) {
        --container: 991.98px;
      }
    } @else if $screen-size == medium {
      @include breakpoint-down(medium) {
        --container: 767.98px;
      }
    } @else if $screen-size == regular {
      @include breakpoint-down(regular) {
        --container: 575.98px;
      }
    } @else if $screen-size == small {
      @include breakpoint-down(small) {
        --container: 480.98px;
      }
    } @else if $screen-size == xsmall {
      @include breakpoint-down(xsmall) {
        --container: 100%;
      }
    }
  }
}
