@use 'additional/mixins' as *;

.hero {
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  &__wrapp {
    display: flex;
    gap: 50px;
  }

  &__img {
    max-width: 400px;
  }
  &__name {
    margin-bottom: 10px;
    text-transform: uppercase;
    order: -2;
  }
  &__pos {
    text-transform: uppercase;
    color: rgba(231,189,116,.8);
    order: -2;
    letter-spacing: .3em;
    margin-bottom: 100px;
  }
  &__title {
    max-width: 550px;
    text-align: left;
    text-transform: uppercase;
    opacity: .75;
  }
}

@include breakpoint-down(medium) {
  .hero {
    &__wrapp {
      flex-direction: column;
      align-items: center;
      gap: 15px;
    }

    &__pos {
      margin-bottom: 30px;
    }
  }
}
